const store: { [key: string]: any } = {};

export type CodeMirrorPosition = {
  ch: number;
  line: number;
  sticky?: string;
}

export const setFocusState = (id: string, state: boolean | CodeMirrorPosition) => {
  store[id] = state;
};

export const getFocusState = (id: string): boolean | CodeMirrorPosition => {
  return store[id];
}
